export default {
	props: {
		list: {
			required: true,
			type: Array,
			default: () => []
		},
		autoNext: {
			type: Boolean,
			default: false
		},
		delay: {
			type: Number,
			default: 800
		}
	},
	data() {
		return {
			selectOptionId: null,
			currentIndex: 0
		};
	},
	computed: {
		question() {
			const index = this.currentIndex;
			return this.list[index];
		},
		isRight() {
			const question = this.question;
			const id = this.selectOptionId;
			if (!id) {
				return null;
			}
			if (id === question.answer) {
				return true;
			} else {
				return false;
			}
		}
	},
	methods: {
		handleSelect(evt) {
			if (this.selectOptionId != null) return
			this.selectOptionId = evt;
			const question = this.question;
			if (this.autoNext) {
				this.turnToNext(this.delay);
			}
		},
		turnToNext(delay = 0) {
			setTimeout(() => {
				this.changeIndex()
				this.selectOptionId = null;
			}, delay);
		},
		changeIndex() {
			const length = this.list.length
			if (this.currentIndex + 1 < length) {
				this.currentIndex += 1;
			} else {
				this.currentIndex = 0
			}
		}
	}
};
